// default

import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// Pages

import Home from '@/pages/Home.vue'
import NotFound from '@/pages/404.vue'
import Note from '@/pages/Note.vue'



// Routering

export default new Router ({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '*',
            name: 'NotFound',
            component: NotFound
        },
        {
            path: '/note/:id',
            name: 'note',
            component: Note
        },
    ]
}) 